@import url('https://fonts.googleapis.com/css?family=JetBrains+Mono&display=swap');

html {
    background-color: #222222;
    color: #BBBBBB;
    font-family: 'JetBrains Mono', monospace;
    font-size: 24px;
}

.App {
    position: relative;
}

.cardList {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 1rem 1rem 0 1rem;
    position: relative;
}

.timeAgoCard {
    position: relative;
    width: clamp(22rem, 100% - 5rem, 30rem);
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    background-color: #333333;
    border-radius: 0.5rem;
    padding: 0;
    align-items: center;
}

.xMinAgo {
    margin: 0.1rem auto 0.1rem 0.5rem;
    text-align: center;
}

.timeStamp {
    margin: 0;
    text-align: right;
    width: 8.3rem;
    border-radius: 0.5rem;
    box-sizing: border-box;
    padding: 0.1rem 0.5rem 0.1rem 0.5rem;
    background-color: #444444;
    position: absolute;
    right: 0;
    transition: 0.1s ease-in-out;
    z-index: 10;
    box-shadow: 0 0 0.6rem -0.2rem black;
}

.timeStampShadow {
    margin: 0;
    height: 100%;
    width: 8.3rem;
    box-sizing: border-box;
    background-color: transparent;
    position: absolute;
    right: 0;
    z-index: 15;
}

.timeStampShadow:hover + .timeStamp {
    transform: translateX(-8.3rem)
}

.delButton {
    width: 8.3rem;
    background-color: transparent;
    border-color: transparent;
    color: #BB6666;
    margin: 0;
    text-align: center;
    vertical-align: middle;
    box-sizing: border-box;
    height: 1.4rem;
    padding: 0;
}

.newTimeAgoBlock {
    padding-bottom: 1rem;
    margin: 0;
    display: flex;
    flex-direction: horizontal;
    position:relative;
    width: clamp(27rem, 100%, 35rem);
}

.newTimeAgoInputBlock {
    padding: 0.5rem 0 0.5rem 0;
    display: flex;
    flex-direction: horizontal;
    gap: 0.5rem;
    position:relative;
    margin-right: auto;
}

.newTimeAgoText {
    margin: 0.15rem 0 0 0;
    padding: 0;
}

.newTimeAgoInput {
    background: #333333;
    border: transparent;
    width: 3rem;
    height: 1.5rem;
    border-radius: 0.5rem;
    color: #BBBBBB;
    font-family: 'JetBrains Mono';
    text-align: center;
    font-size: 1rem;
}

.newTimeAgoSubmit {
    background-color: #333333;
    border: transparent;
    font-family: 'JetBrains Mono';
    font-size: 1rem;
    color: #BBBBBB;
    padding: 0.5rem;
    border-radius: 0.5rem;
    transition-duration: 0.2s;
}

.newTimeAgoSubmit:hover {
    background-color: #444444;
}

.errorText {
    position: absolute;
    height: 1rem;
    color: #BB6666;
    font-size: 0.8rem;
    margin: 0;
    padding: 0;
    top: 3.5rem;
}